import React, { PropTypes, Component } from 'react'

import NewPositionFormDialog from './NewPositionFormDialog'

class PositionListToolbar extends Component {
  constructor (props) {
    super(props)
    this.state = { bottomDialogVisible: false }
  }

  closeBottomDialog = (event) => {
    event && event.preventDefault()
    this.setState({ bottomDialogVisible: !this.state.bottomDialogVisible })
  }

  render () {
    const { bottomDialogVisible } = this.state
    const { createCallback } = this.props
    return (
      <div className='toolbar'>
        <button className='btn btn-primary'
          onClick={() => this.setState({ bottomDialogVisible: true })}>
          <i className='fa fa-plus' />创建新职位
        </button>

        {bottomDialogVisible
          ? <NewPositionFormDialog isVisible={bottomDialogVisible}
            createCallback={createCallback}
            closeCallback={this.closeBottomDialog} />
          : null
        }
      </div>
    )
  }
}

PositionListToolbar.propTypes = {
  createCallback: PropTypes.func
}

export default PositionListToolbar



