import React, { PropTypes, Component } from 'react'
import { Field, reduxForm } from 'redux-form'
import { graphql } from 'react-apollo'

import { renderTextArea, renderSelect } from 'components/FormControls'

import Dialog from 'components/Dialog'

import './PositionListToolbar.scss'

import POSITION_PARAMS_QUERY from '../graphql/PositionParamsQuery.graphql'
import CREATE_POSITION_MUTATION from '../graphql/CreatePositionMutation.graphql'

const validate = values => {
  const errors = {}
  if (!values.emplPositionClassId) {
    errors.emplPositionClassId = '必填'
  }
  return errors
}

const options = [
  { value: 'Y', label: '是' },
  { value: 'N', label: '否' }
]

let NewPositionForm = ({ handleSubmit, submitCallback, cancelCallback, statusOptions, classOptions }) => (
  <form onSubmit={handleSubmit(submitCallback)} className='form-horizontal'>
    <Field name='emplPositionClassId' component={renderSelect} options={classOptions} label='职位头衔' />
    <Field name='statusId' component={renderSelect} options={statusOptions} label='状态' />
    <Field name='description' component={renderTextArea} rows={4} label='描述' />
    <Field name='fullTimeFlag' component={renderSelect} options={options} label='全职' />
    <Field name='virtualFlag' component={renderSelect} options={options} label='虚拟' />
    <Field name='virtualRootFlag' component={renderSelect} options={options} label='虚拟顶层' />
    <div className='form-group'>
      <div className='col-sm-4 col-sm-offset-2'>
        <button className='btn btn-primary' type='submit'><i className='fa fa-plus' />创建</button>
        <button className='btn' onClick={cancelCallback}>取消</button>
      </div>
    </div>
  </form>
)
NewPositionForm.propTypes = {
  handleSubmit: PropTypes.func,
  submitCallback: PropTypes.func,
  cancelCallback: PropTypes.func,
  statusOptions: PropTypes.array,
  classOptions: PropTypes.array
}
NewPositionForm = reduxForm({
  form: 'NewPositionForm',
  validate
})(NewPositionForm)

class NewPositionFormDialog extends Component {
  createNewPosition = (data) => {
    const { closeCallback, createCallback } = this.props
    this.props.createEmplPosition(data).then(() => {
      createCallback && createCallback()
      closeCallback && closeCallback()
    })
  }

  render () {
    const { isVisible, emplPositionClasses, statusItems, closeCallback } = this.props

    const statusOptions = statusItems
      ? statusItems.map((it) => {
        return { value: it.node.statusId, label: it.node.description }
      })
      : []
    const classOptions = emplPositionClasses
      ? emplPositionClasses.map(it => {
        return { value: it.node.emplPositionClassId, label: it.node.title }
      })
      : []

    return (
      <Dialog isVisible={isVisible} size={600} position='right' title='创建新职位'
        onClose={closeCallback}>
        <NewPositionForm submitCallback={this.createNewPosition}
          cancelCallback={closeCallback}
          statusOptions={statusOptions}
          classOptions={classOptions}
          initialValues={{
            statusId: 'EmpsPlanned',
            fullTimeFlag: 'Y',
            virtualFlag: 'N',
            virtualRootFlag: 'N'
          }}
        />
      </Dialog>
    )
  }
}

NewPositionFormDialog.propTypes = {
  isVisible: PropTypes.bool,
  emplPositionClasses: PropTypes.array,
  statusItems: PropTypes.array,
  createCallback: PropTypes.func,
  closeCallback: PropTypes.func,
  createEmplPosition: PropTypes.func
}

let NewPositionFormDialogWithData = graphql(POSITION_PARAMS_QUERY, {
  props: ({ ownProps, data: { loading, moqui, mantle } }) => {
    return {
      ...ownProps,
      loading,
      statusItems: moqui && moqui.statusItems && moqui.statusItems.edges,
      emplPositionClasses: mantle && mantle.emplPositionClasses && mantle.emplPositionClasses.edges
    }
  }
})(NewPositionFormDialog)

NewPositionFormDialogWithData = graphql(CREATE_POSITION_MUTATION, {
  props: ({ mutate }) => {
    return {
      createEmplPosition: ({
        emplPositionClassId,
        organizationPartyId,
        description,
        statusId,
        fullTimeFlag,
        virtualFlag,
        virtualRootFlag
      }) =>
        mutate({
          variables: {
            emplPositionClassId,
            organizationPartyId,
            description,
            statusId,
            fullTimeFlag,
            virtualFlag,
            virtualRootFlag
          }
        })
    }
  }
})(NewPositionFormDialogWithData)

export default NewPositionFormDialogWithData



