import React, { PropTypes } from 'react'

const getData = (source, path) => {
  let properties = path.split('.')
  let result = source
  for (let property of properties) {
    result = result[property]
    if (result === undefined || result === null) return result
  }
  return result
}

const MoshiTable = ({ columns, dataSource }) => (
  <table className='table table-striped'>
    <thead>
      <tr>
        {columns.map((column, i) => (
          <th key={i}>{column.title}</th>
        ))}
      </tr>
    </thead>
    <tbody>
      {dataSource && dataSource.map((it, index) => {
        return (
          <tr key={index}>
            {columns.map((column, i) => (
              <td key={i}>
                {column.renderCell ? column.renderCell(getData(it, column.dataPath)) : getData(it, column.dataPath)}
              </td>
            ))}
          </tr>
        )
      })}
    </tbody>
  </table>
)

MoshiTable.propTypes = {
  columns: PropTypes.array,
  dataSource: PropTypes.array
}

export default MoshiTable



