import React, { Component, PropTypes } from 'react'
import { graphql } from 'react-apollo'
import { Link } from 'react-router'

import MoshiTable from 'components/MoshiTable'
import { Pagination } from 'lib/react-bootstrap-fix'

import { prefixLink } from 'lib/utils'

import PositionListToolbar from './components/PositionListToolbar'

import POSITION_LIST_QUERY from './graphql/PositionListQuery.graphql'

const tableColumns = [
  { title: '序号',
    dataPath: 'node.emplPositionId',
    renderCell: (data) => (<Link to={prefixLink(`/positions/${data}`)}>{data}</Link>)
  },
  { title: '职位名称', dataPath: 'node.emplPositionClass.title' },
  { title: '全职', dataPath: 'node.fullTimeFlag' },
  { title: '虚拟', dataPath: 'node.virtualFlag' },
  { title: '虚拟顶级', dataPath: 'node.virtualRootFlag' },
  { title: '状态', dataPath: 'node.status.description' }
]

const PositionList = ({ emplPositions }) => (
  <MoshiTable columns={tableColumns} dataSource={emplPositions} />
)
PositionList.propTypes = {
  emplPositions: PropTypes.array
}

class PositionsIndexContainer extends Component {
  handleCreateCallback = () => {
    this.props.data.refetch()
  }

  handlePaginationChange = (eventKey) => {
    const { pageInfo, loadMoreEntries } = this.props
    if (eventKey - 1 === pageInfo.pageIndex) return
    loadMoreEntries(eventKey - 1)
  }

  render () {
    const { emplPositions, pageInfo } = this.props
    return (
      <div>
        <PositionListToolbar createCallback={this.handleCreateCallback} />
        <div>
          <PositionList emplPositions={emplPositions} />
        </div>
        <div>
          <Pagination prev next first last pageInfo={pageInfo}
            onSelect={this.handlePaginationChange} />
        </div>
      </div>
    )
  }
}

PositionsIndexContainer.propTypes = {
  loading: PropTypes.bool,
  routes: PropTypes.array,
  pattern: PropTypes.string,
  data: PropTypes.object,
  emplPositions: PropTypes.array,
  pageInfo: PropTypes.object,
  loadMoreEntries: PropTypes.func
}

const PositionsIndexContainerWithData = graphql(POSITION_LIST_QUERY, {
  options (props) {
    return {
      variables: {
        pageIndex: 0
      }
    }
  },
  props: ({ ownProps, data }) => {
    const { loading, mantle, fetchMore } = data
    return {
      ...ownProps,
      loading,
      data,
      emplPositions: mantle && mantle.emplPositions && mantle.emplPositions.edges,
      pageInfo: mantle && mantle.emplPositions && mantle.emplPositions.pageInfo,

      loadMoreEntries (pageIndex) {
        return fetchMore({
          variables: { pageIndex },
          updateQuery: (previousResult, { fetchMoreResult }) => {
            return Object.assign({}, previousResult, {
              mantle: fetchMoreResult.data && fetchMoreResult.data.mantle
            })
          }
        })
      }
    }
  }
})(PositionsIndexContainer)

export default PositionsIndexContainerWithData



