import React, { PropTypes, Component } from 'react'
import Select from 'react-select'

export const renderInput = ({ input, label, type, placeholder, meta: { touched, error } }) => (
  <div className='form-group'>
    <label className='control-label col-sm-2'>{label}</label>
    <div className='col-sm-10'>
      <input {...input}
        {...{ placeholder,
          type,
          className: touched && error ? 'form-control error' : 'form-control'
        }}
      />
      {touched && error && <label className='error'>{error}</label>}
    </div>
  </div>
)
renderInput.propTypes = {
  input: PropTypes.any,
  type: PropTypes.string,
  label: PropTypes.string,
  placeholder: PropTypes.string,
  meta: PropTypes.object
}

export const renderTextArea = ({ input, label, rows, placeholder, meta: { touched, error } }) => (
  <div className='form-group'>
    <label className='control-label col-sm-2'>{label}</label>
    <div className='col-sm-10'>
      <textarea {...input}
        {...{ placeholder,
          rows,
          className: touched && error ? 'form-control error' : 'form-control'
        }}
      />
      {touched && error && <label className='error'>{error}</label>}
    </div>
  </div>
)

renderTextArea.propTypes = {
  input: PropTypes.any,
  label: PropTypes.string,
  rows: PropTypes.number,
  placeholder: PropTypes.string,
  meta: PropTypes.object
}

class SelectInput extends Component {
  onChange = (event) => {
    if (this.props.input.onChange) {
      // To be aligned with how redux-form publishes its CHANGE action payload.
      // The event received is an object with 2 keys: "value" and "label"
      this.props.input.onChange(event && event.value)
    }
  }

  render () {
    return (
      <Select
        {...this.props}
        value={this.props.input.value || ''}
        onBlur={() => this.props.input.onBlur(this.props.input.value)}
        onChange={this.onChange}
        options={this.props.options} // <-- Receive options from the form
      />
    )
  }
}

SelectInput.propTypes = {
  input: PropTypes.any,
  options: PropTypes.array
}

export const renderSelect = (props) => {
  const hasError = props.meta.touched && props.meta.error
  return (
    <div className='form-group'>
      <label className='control-label col-sm-2'>{props.label}</label>
      <div className='col-sm-10'>
        <SelectInput {...props} className={hasError ? 'error' : ''} />
        {hasError && <label className='error'>{props.meta.error}</label>}
      </div>
    </div>
  )
}

renderSelect.propTypes = {
  input: PropTypes.any,
  label: PropTypes.string,
  placeholder: PropTypes.string,
  meta: PropTypes.object
}



