import React, { Component, PropTypes } from 'react'
import { MatchWithRoutes } from 'lib/react-router-addons-routes'

class PositionsContainer extends Component {
  render () {
    const { routes, pattern } = this.props
    return (
      <div>
        <span>PositionsContainer</span>
        <div>
          {routes.map((route, i) => (
            <MatchWithRoutes key={i} {...route} parentPattern={pattern} />
          ))}
        </div>
      </div>
    )
  }
}

PositionsContainer.propTypes = {
  loading: PropTypes.bool,
  routes: PropTypes.array,
  pattern: PropTypes.string
}

export default PositionsContainer



