import React, { PropTypes, Component } from 'react'
import Dock from 'react-dock'

import './Dialog.scss'

const DialogHeader = ({ title }) => (
  <div className='header'>{title}</div>
)
DialogHeader.propTypes = {
  title: PropTypes.string
}

const DialogContent = ({ children }) => (
  <div className='content clearfix'>{children}</div>
)
DialogContent.propTypes = {
  children: PropTypes.node
}

const DialogFooter = ({ children }) => (
  <div className='footer'>{children}</div>
)
DialogFooter.propTypes = {
  children: PropTypes.node
}

class Dialog extends Component {
  render () {
    const { title, position, size, isVisible, onClose } = this.props
    return (
      <Dock position={position || 'right'}
        isVisible={isVisible}
        fluid={false}
        defaultSize={size || 500}>
        <span className='dialog-close' onClick={onClose}><i className='fa fa-remove fa-lg' /></span>

        <div className='dialog-container'>
          <div className='header'>{title}</div>
          {this.props.children}
        </div>

      </Dock>
    )
  }
}

Dialog.propTypes = {
  children: PropTypes.node,
  title: PropTypes.string,
  position: PropTypes.string,
  size: PropTypes.number,
  isVisible: PropTypes.bool,
  onClose: PropTypes.func
}

export default Dialog



