import React, { Component, PropTypes } from 'react'
import { graphql } from 'react-apollo'
import gql from 'graphql-tag'

class PositionOneContainer extends Component {
  render () {
    const { mantle } = this.props
    return (
      <div>
        <span>{JSON.stringify(mantle)}</span>
      </div>
    )
  }
}

PositionOneContainer.propTypes = {
  loading: PropTypes.bool,
  routes: PropTypes.array,
  pattern: PropTypes.string,
  mantle: PropTypes.object
}

const emplPositionQuery = gql`
query {
  mantle {
    emplPosition (emplPositionId: "adfafd") {
      emplPositionId
      emplPositionClass {
        title
      }
    }
  } 
}
`

const PositionOneContainerWithData = graphql(emplPositionQuery, {
  props: ({ ownProps, data: { loading, mantle } }) => {
    return {
      loading,
      mantle
    }
  }
})(PositionOneContainer)

export default PositionOneContainerWithData



